import React from 'react';
import { Button } from 'antd';
import { Link } from 'react-router-dom';
import Result from '../../components/Result';
import styles from './Result.css';

const title = <div className={styles.title}>AntDesign@example.com </div>;

const actions = (
  <div className={styles.actions}>
    <a href=""><Button size="large" type="primary"></Button></a>
    <Link to="/"><Button size="large"></Button></Link>
  </div>
);

export default () => (
  <Result
    className={styles.registerResult}
    type="success"
    title={title}
    description="24"
    actions={actions}
    style={{ marginTop: 56 }}
  />
);



